import React, { useState } from "react";
import schoolLogo from '../pictures/School Logo.jpg';
import rbsLogo from '../pictures/RBS Logo.png'
import deiLogo from '../pictures/Dayalbagh Educational Institute Logo.png'
import AOS from 'aos';
import 'aos/dist/aos.css';
import { useEffect } from 'react';
import {isMobile} from 'react-device-detect';

const menuHeight = getComputedStyle(document.documentElement).getPropertyValue(
  "--menu-height"
);

const buttons = ["B.Tech", "Diploma", "High School"];

const content = [
  {
    photo: rbsLogo,
    heading: "Raja Balwant Singh Engineering Technical Campus",
    specialization: "Computer Science & Engineering",
    para: "2020 - 2023",  
    anchor: "https://www.rbsalumni.in/",
    anchor_txt: "rbsalumni.in"
  },
  {
    photo: deiLogo,
    heading: "DayalBagh Educational Institute",
    specialization: "Information Technology",
    para: "2017 - 2020",
    anchor: "https://www.dei.ac.in/dei/",
    anchor_txt: "dei.ac.in"
  },
  {
    photo: schoolLogo,
    heading: "Symboyzia Senior Secondary School",
    para: "2012 - 2017",
    location: "Agra, Uttar Pradesh", 
    anchor: "https://www.facebook.com/symboyziagroup/",
    anchor_txt: "symboyziaschool.com"
  }
];

// console.log(content[0].heading)

export default function scrollableWidget(props) {
  const [activeBlock, setActiveBlock] = useState(0);

  const toggleMenuBlock = (index) => {
    setActiveBlock(index);
  };

  useEffect(() => {
    AOS.init();
  }, [])

  return (
    <>
      <div className="scrollable-widget" data-aos={`${isMobile ? 'zoom-in' : 'fade-up-right' }`} data-aos-duration="500" data-aos-offset="200">
        <h3 className="mini-heading lightest-slate">{props.heading}</h3>
        <article className={`card open`}>
          <div className="buttons">
            {buttons.map((button, index) => (
              <button key={index} className={index === activeBlock ? "active" : ""} onClick={() => toggleMenuBlock(index)}>
                {button}
              </button>
            ))}
          </div>
          <div className="wrapper z-0">
            <div className="content" style={(window.innerWidth > 648) ? { translate: `0 calc(0px - ${menuHeight} * ${activeBlock})` } : {}}>
              {content.map((item, index) => (
                <div className="block" key={index} style={(window.innerWidth < 648) ? { display: index === activeBlock ? "block" : "none" } : {}}>
                  {item.photo && <img className={`institute-logo ${(item.heading.includes('School')) ? ' school' : ''}`} src={item.photo} alt="" />}
                  <div className="text">
                    <h2 className=" institute-info institute-heading">{item.heading}</h2>
                    {item.specialization && <h3 className="institute-info specialization">{item.specialization}</h3>}
                    <p className="institute-info session">{item.para}</p>
                    {/* <p>{item.anchor && <a className="institute-link" href={item.anchor}><svg viewBox="0 0 100 100" version="1.1"><defs></defs><g id="43.-Globe" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round"><circle id="Layer-1" stroke="#222F3E" stroke-width="4" cx="50" cy="50" r="48"></circle><path d="M50,2 L50,98" id="Layer-2" stroke="#222F3E" stroke-width="4"></path><path d="M98,50 L2,50" id="Layer-3" stroke="#222F3E" stroke-width="4"></path><path d="M50,2 C50,2 74.1434192,20.5422687 74.1434192,50 C74.1434192,79.4577313 50,98 50,98" id="Layer-4" stroke="#222F3E" stroke-width="4"></path><path d="M50.1434192,2 C50.1434192,2 26,20.5422687 26,50 C26,79.4577313 50.1434192,98 50.1434192,98" id="Layer-5" stroke="#222F3E" stroke-width="4"></path><path d="M16.4,83.0279716 C23.8528065,76.4675429 35.3718224,69.2 49.8302321,69.2 C64.5100604,69.2 76.1597767,76.6918401 83.6,83.328848" id="Layer-6" stroke="#222F3E" stroke-width="4"></path><path d="M16.4,16.7008764 C23.8528065,23.2613052 35.3718224,30.528848 49.8302321,30.528848 C64.5100604,30.528848 76.1597767,23.037008 83.6,16.4" id="Layer-7" stroke="#222F3E" style={{strokeWidth: 4}}></path></g></svg> {item.anchor_txt}</a>}</p></div> */}
                    <p>{item.anchor && <a className="institute-info institute-link" rel="noopener noreferrer" target="_blank" href={item.anchor}>{item.anchor_txt} <svg width="18" height="18" viewBox="0 0 18 18"><g><path d="M2.11012,10.92015a3.66047,3.66047,0,0,0,5.02221,5.32243l.15061-.14241,1.91654-1.8907a.5.5,0,0,0,.0048-.70709l-.35115-.35595a.5.5,0,0,0-.70709-.0048L6.22589,15.03592a2.1613,2.1613,0,0,1-3.18828-2.91217l.11806-.12825L6.43742,8.89273a2.16135,2.16135,0,0,1,2.76089-.24667l.138.10474.19292.159a.5.5,0,0,0,.70384-.06786l0,0,.318-.38586a.5.5,0,0,0-.06787-.70384l-.19293-.159a3.6574,3.6574,0,0,0-4.74838.08239Z" fill="var(--iconFill, #464646)" style={{fill: 'inherit'}}></path><path d="M15.59933,7.78294a3.65988,3.65988,0,0,0-5.03516-5.30931l-.152.14387-1.9896,2.073a.5.5,0,0,0,.01447.707l0,0,.36073.34623a.5.5,0,0,0,.70694-.0145l1.97885-2.062a2.15967,2.15967,0,0,1,3.15327,2.94839l-3.211,3.45863a2.16059,2.16059,0,0,1-2.9027.13873l-.13055-.11719-.1792-.17432a.5.5,0,0,0-.707.00976l-.34863.3584a.5.5,0,0,0,.00976.707l.1792.17432a3.66324,3.66324,0,0,0,5.00211.09236l.157-.1484Z" fill="var(--iconFill, #464646)" style={{fill: 'inherit'}}></path></g></svg></a>}</p></div>
                </div>
              ))}
            </div>
          </div>
        </article>
      </div>
    </>
  );
};