import React from 'react';
import {isMobile} from 'react-device-detect';

export default function setting(props) {

  const att = {
    onClick: isMobile ? null : props.toggle
  }

  return (
    <>
      <div className={`settings-overlay ${(props.d) ? 'show' : ''}`} {...att} onTouchStart={props.toggle}></div>
      <div className={`settings-panel ${(props.d) ? 'show' : ''}`} onScroll={(e) => {e.stopPropagation()}}>
        <ul className='nav-list side-panel'>
          <li className='nav-item' style={{ '--i': '1' }} onClick={props.toggle}>
            <a href='#about' className='nav-link'><span className='section-number'><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" style={{strokeWidth: '2px', strokeLinecap: 'round'}} className="feather feather-info"><circle cx="12" cy="12" r="10"></circle><line x1="12" y1="16" x2="12" y2="12"></line><line x1="12" y1="8" x2="12.01" y2="8"></line></svg> </span>About</a>
          </li>
          <li className='nav-item' style={{ '--i': '2' }} onClick={props.toggle}>
            <a href='#skills' className='nav-link'><span className='section-number'><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" style={{strokeWidth: '2px'}} className="feather feather-star"><polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2"></polygon></svg> </span>Skills</a>
          </li>
          <li className='nav-item' style={{ '--i': '3' }} onClick={props.toggle}>
            <a href='#projects' className='nav-link'><span className='section-number'><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" style={{strokeWidth: '2px'}} className="feather feather-code"><polyline points="16 18 22 12 16 6"></polyline><polyline points="8 6 2 12 8 18"></polyline></svg> </span>Projects</a>
          </li>
          <li className='nav-item' style={{ '--i': '3' }} onClick={props.toggle}>
            <a href='#contact' className='nav-link'><span className='section-number'><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" style={{strokeWidth: '2px'}} className="feather feather-user-plus"><path d="M16 21v-2a4 4 0 0 0-4-4H5a4 4 0 0 0-4 4v2"></path><circle cx="8.5" cy="7" r="4"></circle><line x1="20" y1="8" x2="20" y2="14"></line><line x1="23" y1="11" x2="17" y2="11"></line></svg> </span>Contact</a>
          </li>
        </ul>
        <h3 className='flex mt-4 mb-4'>
          Settings
          <button id='back-btn' onClick={props.toggle}>
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="feather feather-x"><line x1="18" y1="6" x2="6" y2="18"></line><line x1="6" y1="6" x2="18" y2="18"></line></svg>
          </button>
        </h3>
        <p className='mb-3'>Icons style </p>
        <input type="radio" name="icon" id="fill" hidden defaultChecked onChange={props.changeStyle} />
        <label htmlFor="fill">
          <div className={`option social-icon fill`}>
            <ul>
              <li title="facebook" style={{ '--i': '1' }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="feather feather-facebook"><path d="M18 2h-3a5 5 0 0 0-5 5v3H7v4h3v8h4v-8h3l1-4h-4V7a1 1 0 0 1 1-1h3z"></path></svg>
                <i className="fa-brands fa-facebook-f"></i>
              </li>
              <li title="Instagram" style={{ '--i': '2' }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="feather feather-instagram"><rect x="2" y="2" width="20" height="20" rx="5" ry="5"></rect><path d="M16 11.37A4 4 0 1 1 12.63 8 4 4 0 0 1 16 11.37z"></path><line x1="17.5" y1="6.5" x2="17.51" y2="6.5"></line></svg>
                <i className="fa-brands fa-instagram"></i>
              </li>
              <li title="Github" style={{ '--i': '3' }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="feather feather-github"><path d="M9 19c-5 1.5-5-2.5-7-3m14 6v-3.87a3.37 3.37 0 0 0-.94-2.61c3.14-.35 6.44-1.54 6.44-7A5.44 5.44 0 0 0 20 4.77 5.07 5.07 0 0 0 19.91 1S18.73.65 16 2.48a13.38 13.38 0 0 0-7 0C6.27.65 5.09 1 5.09 1A5.07 5.07 0 0 0 5 4.77a5.44 5.44 0 0 0-1.5 3.78c0 5.42 3.3 6.61 6.44 7A3.37 3.37 0 0 0 9 18.13V22"></path></svg>
                <i className="fa-brands fa-github"></i>
              </li>
              <li title="LinkedIn" style={{ '--i': '4' }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="feather feather-linkedin"><path d="M16 8a6 6 0 0 1 6 6v7h-4v-7a2 2 0 0 0-2-2 2 2 0 0 0-2 2v7h-4v-7a6 6 0 0 1 6-6z"></path><rect x="2" y="9" width="4" height="12"></rect><circle cx="4" cy="4" r="2"></circle></svg>
                <i className="fa-brands fa-linkedin-in"></i>
              </li>
              <li title="Codepen" style={{ '--i': '5' }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="feather feather-codepen"><polygon points="12 2 22 8.5 22 15.5 12 22 2 15.5 2 8.5 12 2"></polygon><line x1="12" y1="22" x2="12" y2="15.5"></line><polyline points="22 8.5 12 15.5 2 8.5"></polyline><polyline points="2 15.5 12 8.5 22 15.5"></polyline><line x1="12" y1="2" x2="12" y2="8.5"></line></svg>
                <i className="fa-brands fa-codepen"></i>
              </li>
            </ul>
          </div>
        </label>
        <input type="radio" name="icon" id="stroke" hidden onChange={props.changeStyle} />
        <label htmlFor="stroke">
          <div className={`option social-icon stroke`}>
            <ul>
              <li title="facebook" style={{ '--i': '1' }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="feather feather-facebook"><path d="M18 2h-3a5 5 0 0 0-5 5v3H7v4h3v8h4v-8h3l1-4h-4V7a1 1 0 0 1 1-1h3z"></path></svg>
                <i className="fa-brands fa-facebook-f"></i>
              </li>
              <li title="Instagram" style={{ '--i': '2' }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="feather feather-instagram"><rect x="2" y="2" width="20" height="20" rx="5" ry="5"></rect><path d="M16 11.37A4 4 0 1 1 12.63 8 4 4 0 0 1 16 11.37z"></path><line x1="17.5" y1="6.5" x2="17.51" y2="6.5"></line></svg>
                <i className="fa-brands fa-instagram"></i>
              </li>
              <li title="Github" style={{ '--i': '3' }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="feather feather-github"><path d="M9 19c-5 1.5-5-2.5-7-3m14 6v-3.87a3.37 3.37 0 0 0-.94-2.61c3.14-.35 6.44-1.54 6.44-7A5.44 5.44 0 0 0 20 4.77 5.07 5.07 0 0 0 19.91 1S18.73.65 16 2.48a13.38 13.38 0 0 0-7 0C6.27.65 5.09 1 5.09 1A5.07 5.07 0 0 0 5 4.77a5.44 5.44 0 0 0-1.5 3.78c0 5.42 3.3 6.61 6.44 7A3.37 3.37 0 0 0 9 18.13V22"></path></svg>
                <i className="fa-brands fa-github"></i>
              </li>
              <li title="LinkedIn" style={{ '--i': '4' }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="feather feather-linkedin"><path d="M16 8a6 6 0 0 1 6 6v7h-4v-7a2 2 0 0 0-2-2 2 2 0 0 0-2 2v7h-4v-7a6 6 0 0 1 6-6z"></path><rect x="2" y="9" width="4" height="12"></rect><circle cx="4" cy="4" r="2"></circle></svg>
                <i className="fa-brands fa-linkedin-in"></i>
              </li>
              <li title="Codepen" style={{ '--i': '5' }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="feather feather-codepen"><polygon points="12 2 22 8.5 22 15.5 12 22 2 15.5 2 8.5 12 2"></polygon><line x1="12" y1="22" x2="12" y2="15.5"></line><polyline points="22 8.5 12 15.5 2 8.5"></polyline><polyline points="2 15.5 12 8.5 22 15.5"></polyline><line x1="12" y1="2" x2="12" y2="8.5"></line></svg>
                <i className="fa-brands fa-codepen"></i>
              </li>
            </ul>
          </div>
        </label>
        <p className="flex mt-4 mb-4">Theme:</p>
        <div className="theme-section">
          <div className="theme-option">
            <input type="radio" name="theme" className='theme-option-color' id="#00ffff" hidden defaultChecked onChange={props.changeTheme} /> 
            <label htmlFor="#00ffff" className={`theme-option__color`} style={{ background: '#00ffff'}}></label>
            
            <input type="radio" name="theme" className='theme-option-color' id="#5f9ea0" hidden onChange={props.changeTheme} /> 
            <label htmlFor="#5f9ea0" className={`theme-option__color`} style={{ background: '#5f9ea0'}}></label>

            <input type="radio" name="theme" className='theme-option-color' id="#fff533" hidden onChange={props.changeTheme} /> 
            <label htmlFor="#fff533" className={`theme-option__color`} style={{ background: '#fff533'}}></label>

            <input type="radio" name="theme" className='theme-option-color' id="#e28333" hidden onChange={props.changeTheme} /> 
            <label htmlFor="#e28333" className={`theme-option__color`} style={{ background: '#e28333'}}></label>
          </div>
        </div>
      </div>
    </>
  )
}
