import React, { useState, useEffect } from 'react';
import Setting from './setting';

export default function header(props) {

    const scrollDirection = useScrollDirection();

    function handleScrollOnDisplay() {
        if (props.d) {
            document.body.style.overflow = 'hidden';
        }
        else {
            document.body.style.overflow = 'auto';
        }
    }

    useEffect(() => {
        handleScrollOnDisplay();
    }, [props.d]);

    return (
        <>
            <nav className={`navigation ${scrollDirection[0] === "down" ? ((props.d) ? '' :'slide-up') : ''} ${(document.scrollY > 0) ? 'shadow-with-blur' : ''} ${scrollDirection[1] ? ((props.d) ? 'shadow' :'shadow-with-blur') : 'no-shadow'}`} >
                <div className='logo-box outer'>
                    <div className='logo-box'>
                        <h1 className='logo'>R</h1>
                    </div>
                </div>
                <ul className='nav-list'>
                    <li className='nav-item' style={{ '--i': '1' }}>
                        <a href='#about' className='nav-link'><span className='section-number'><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" style={{strokeWidth: '2px', strokeLinecap: 'round'}} className="feather feather-info"><circle cx="12" cy="12" r="10"></circle><line x1="12" y1="16" x2="12" y2="12"></line><line x1="12" y1="8" x2="12.01" y2="8"></line></svg> </span>About</a>
                    </li>
                    <li className='nav-item' style={{ '--i': '2' }}>
                        <a href='#skills' className='nav-link'><span className='section-number'><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" style={{strokeWidth: '2px'}} className="feather feather-star"><polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2"></polygon></svg> </span>Skills</a>
                    </li>
                    <li className='nav-item' style={{ '--i': '3' }}>
                        <a href='#projects' className='nav-link'><span className='section-number'><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" style={{strokeWidth: '2px'}} className="feather feather-code"><polyline points="16 18 22 12 16 6"></polyline><polyline points="8 6 2 12 8 18"></polyline></svg> </span>Projects</a>
                    </li>
                    <li className='nav-item' style={{ '--i': '3' }}>
                        <a href='#contact' className='nav-link'><span className='section-number'><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" style={{strokeWidth: '2px'}} className="feather feather-user-plus"><path d="M16 21v-2a4 4 0 0 0-4-4H5a4 4 0 0 0-4 4v2"></path><circle cx="8.5" cy="7" r="4"></circle><line x1="20" y1="8" x2="20" y2="14"></line><line x1="23" y1="11" x2="17" y2="11"></line></svg></span>Contact</a>
                    </li>
                </ul>
                <Setting icon_style={props.icon_style} changeTheme={props.changeTheme} changeStyle={props.changeStyle} d={props.d} toggle={props.toggle}/>
                <button id='setting-btn' onClick={props.toggle}>
                    <div className={`menu ${(props.d) ? 'close' : ''}`}>
                        <div className="menu-bar"></div>
                    </div>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="feather feather-settings"><circle cx="12" cy="12" r="3"></circle><path d="M19.4 15a1.65 1.65 0 0 0 .33 1.82l.06.06a2 2 0 0 1 0 2.83 2 2 0 0 1-2.83 0l-.06-.06a1.65 1.65 0 0 0-1.82-.33 1.65 1.65 0 0 0-1 1.51V21a2 2 0 0 1-2 2 2 2 0 0 1-2-2v-.09A1.65 1.65 0 0 0 9 19.4a1.65 1.65 0 0 0-1.82.33l-.06.06a2 2 0 0 1-2.83 0 2 2 0 0 1 0-2.83l.06-.06a1.65 1.65 0 0 0 .33-1.82 1.65 1.65 0 0 0-1.51-1H3a2 2 0 0 1-2-2 2 2 0 0 1 2-2h.09A1.65 1.65 0 0 0 4.6 9a1.65 1.65 0 0 0-.33-1.82l-.06-.06a2 2 0 0 1 0-2.83 2 2 0 0 1 2.83 0l.06.06a1.65 1.65 0 0 0 1.82.33H9a1.65 1.65 0 0 0 1-1.51V3a2 2 0 0 1 2-2 2 2 0 0 1 2 2v.09a1.65 1.65 0 0 0 1 1.51 1.65 1.65 0 0 0 1.82-.33l.06-.06a2 2 0 0 1 2.83 0 2 2 0 0 1 0 2.83l-.06.06a1.65 1.65 0 0 0-.33 1.82V9a1.65 1.65 0 0 0 1.51 1H21a2 2 0 0 1 2 2 2 2 0 0 1-2 2h-.09a1.65 1.65 0 0 0-1.51 1z"></path></svg>
                </button>
            </nav>
        </>
    )
}

function useScrollDirection() {
    const [scrollDirection, setScrollDirection] = useState(null);
    const [boxShadow, setBoxShadow] = useState(false);

    useEffect(() => {
        let lastScrollY = window.pageYOffset;

        const updateScrollDirection = () => {
            const scrollY = window.pageYOffset;
            const direction = scrollY > lastScrollY ? "down" : "up";
            if (direction !== scrollDirection && (scrollY - lastScrollY > 5 || scrollY - lastScrollY < -5)) {
                setScrollDirection(direction);
            }
            if (scrollY > window.innerHeight / 5)
                    setBoxShadow(true);
                else
                    setBoxShadow(false);

            lastScrollY = scrollY > 0 ? scrollY : 0;
        };
        window.addEventListener("scroll", updateScrollDirection); // add event listener
        return () => {
            window.removeEventListener("scroll", updateScrollDirection); // clean up
        }
    }, [scrollDirection, boxShadow]);

    return [scrollDirection, boxShadow];
};
