import React from 'react'
// import Certificates from './certificates';

const devSkills = [
  {
    heading: "Web Development",
    progress: 80
  },
  {
    heading: "App Development",
    progress: 60
  },
  {
    heading: "UI/UX Design",
    progress: 70
  }
]

const FrontendSkills = [
  {
    heading: "HTML5",
    progress: 88
  },
  {
    heading: "CSS3",
    progress: 90
  },
  {
    heading: "Javascript",
    progress: 85
  },
  {
    heading: "ReactJS",
    progress: 60
  },
  {
    heading: "Wordpress",
    progress: 60
  },
  {
    heading: "Java Swing UI",
    progress: 75
  }
];

const lib = [
  {
    heading: "jQuery",
    progress: 80
  },
  {
    heading: "Boostrap 6",
    progress: 87
  },
  {
    heading: "TailwindCSS",
    progress: 72
  }
]

const BackendSkills = [
  {
    heading: "Java",
    progress: 75
  },
  {
    heading: "Python",
    progress: 57
  },
  {
    heading: "Node JS",
    progress: 80
  }
];

const VCSkills = [
  {
    heading: "Git / Github",
    progress: 45
  }
];

const Databases = [
  {
    heading: "Oracle",
    progress: 75
  },
  {
    heading: "MySQL",
    progress: 67
  },
  {
    heading: "Mongo DB",
    progress: 30
  }
];

export default function skills(props) {
  return (
    <section id='skills'>
      <h2 className='section-heading reversed'>
        <span className='section-icon'><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" style={{ strokeWidth: '2px' }} className="feather feather-star"><polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2"></polygon></svg> </span>
        Skills
      </h2>
      <div className="flex skills-container">
        <div className="flex-1">
          <div className="skill-card" data-aos="fade" data-aos-duration="1000" data-aos-anchor-placement="bottom-bottom">
            <div className="skill-card-header">
              <h4 className="mt-2">Development Skills</h4>
            </div>
            <div className="card-body dev-skills fill pb-2 flex" id="skills">
              {devSkills.map((skill, index) => (
                <div className='skill' key={index} data-aos="fade" data-aos-duration="500" data-aos-delay={`${300 + (index * 100)}`}>
                  <h6 className='skill-name'>{skill.heading}</h6>
                  <div className="circular-progress" >
                    <svg role="progressbar" width="200" height="200" viewBox="0 0 100 100" >
                      <circle id='progress-bg' cx="50%" cy="50%" r="42" fill="none" ></circle>
                      <circle id='progress' cx="50%" cy="50%" r="42" fill="none"></circle>
                      <text id='progress-text' className="pie-text-13" x="50%" y="50%" fill="#fff" dy="0.5rem">
                        <tspan className="pie-percent-13">{skill.progress}</tspan><tspan className="pie-unit-13">%</tspan>
                      </text>
                    </svg>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
      <div className="flex skills-container mt-4">
        <div className="flex-1">
          <div className="skill-card  fill" data-aos="fade" data-aos-duration="1000">
            <div className="skill-card-header">
              <h4 className="mt-2">Frontend</h4>
            </div>
            <div className="card-body pb-2" id="skills">
              {FrontendSkills.map((skill, index) => (
                <div className='skill' key={index}>
                  <h6 className='skill-name'>{skill.heading} <span id='percent'>{skill.progress} %</span> </h6>
                  <div className="progress mb-3">
                    <div className="progress-bar bg-danger" role="progressbar" style={{ width: `${skill.progress}%` }}></div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div className="flex-1 flex column ">
          <div className="skill-card backend fill" data-aos="fade" data-aos-duration="1000">
            <div className="skill-card-header">
              <h4 className="mt-2">Backend</h4>
            </div>
            <div className="card-body pb-2" id="skills">
              {BackendSkills.map((skill, index) => (
                <div className='skill' key={index}>
                  <h6 className='skill-name'>{skill.heading} <span id='percent'>{skill.progress} %</span> </h6>
                  <div className="progress mb-3">
                    <div className="progress-bar bg-danger" role="progressbar" style={{ width: `${skill.progress}%` }}></div>
                  </div>
                </div>
              ))}
            </div>
          </div>
          <div className="skill-card  fill" data-aos="fade" data-aos-duration="1000">
            <div className="skill-card-header">
              <h4 className="mt-2">Version Control</h4>
            </div>
            <div className="card-body pb-2" id="skills">
              {VCSkills.map((skill, index) => (
                <div className='skill' key={index}>
                  <h6 className='skill-name'>{skill.heading} <span id='percent'>{skill.progress} %</span> </h6>
                  <div className="progress mb-3">
                    <div className="progress-bar bg-danger" role="progressbar" style={{ width: `${skill.progress}%` }}></div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div className="flex-1 flex column">
          <div className="skill-card  fill" data-aos="fade" data-aos-duration="1000">
            <div className="skill-card-header">
              <h4 className="mt-2">Libraries</h4>
            </div>
            <div className="card-body pb-2" id="skills">
              {lib.map((skill, index) => (
                <div className='skill' key={index}>
                  <h6 className='skill-name'>{skill.heading} <span id='percent'>{skill.progress} %</span> </h6>
                  <div className="progress mb-3">
                    <div className="progress-bar bg-danger" role="progressbar" style={{ width: `${skill.progress}%` }}></div>
                  </div>
                </div>
              ))}
            </div>
          </div>
          <div className="skill-card  fill" data-aos="fade" data-aos-duration="1000">
            <div className="skill-card-header">
              <h4 className="mt-2">Databases</h4>
            </div>
            <div className="card-body pb-2" id="skills">
              {Databases.map((skill, index) => (
                <div className='skill' key={index}>
                  <h6 className='skill-name'>{skill.heading} <span id='percent'>{skill.progress} %</span> </h6>
                  <div className="progress mb-3">
                    <div className="progress-bar bg-danger" role="progressbar" style={{ width: `${skill.progress}%` }}></div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
      {/* <Certificates/> */}
    </section>
  )
}
