import React, { useState } from 'react';
import './App.css';
import './Loader.css'
import Loader from './components/loader';
import Intro from './components/intro_section';
// import Setting from './components/setting';
import Header from './components/header';
import About from './components/about';
import Skills from './components/skills';
import Projects from './components/projects';
import Contact from './components/contact';
import Gototop from './components/gototop';

function App() {

  const [displaySettings, setDisplaySettings] = useState(false);

  const [icon_style, setIconStyle] = useState("fill");

  function hexToRgb(hex) {
    var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result ? {
      r: parseInt(result[1], 16),
      g: parseInt(result[2], 16),
      b: parseInt(result[3], 16)
    } : null;
  }

  function changeTheme(event) {
    const root = document.querySelector(":root");
    root.style.setProperty("--color-primary", `${hexToRgb(event.target.id).r}, ${hexToRgb(event.target.id).g}, ${hexToRgb(event.target.id).b}`);
    console.log(hexToRgb(event.target.id));
  }

  function toggleSettings() {
    if (displaySettings === false) {
      setDisplaySettings(true);
    } else {
      setDisplaySettings(false);
    }
  }

  function changeIconStyle(event) {
    if (event.target.id === "fill") {
      setIconStyle("fill");
    } else if (event.target.id === "stroke") {
      setIconStyle("stroke");
    }
  }

  document.addEventListener('pointermove', e => {
    const cursor = document.querySelector('.shadow-circle');
    const rect = document.documentElement.getBoundingClientRect();
    cursor.style.setProperty("--x", e.clientX - rect.left);
    cursor.style.setProperty("--y", e.clientY - rect.top);
  });

  return (
    <>
      <div className="shadow-circle" />
      <Loader />
      <Header d={displaySettings} icon_style={icon_style} changeTheme={changeTheme} changeStyle={changeIconStyle} toggle={toggleSettings} />
      <Intro icon_style={icon_style} changeStyle={changeIconStyle} d={displaySettings} toggle={toggleSettings} />
      {/* -<Setting icon_style={icon_style}  changeStyle={changeIconStyle} d={displaySettings} toggle={toggleSettings}/> */}
      <Gototop />
      <About />
      <Skills />
      <Projects />
      <Contact />
    </>
  );
}

export default App;
