function moveForward() {
  const projectImages = document.querySelectorAll('.project-image');
  const projectText = document.querySelectorAll('.project-text .text-container');
  const projectNum = document.querySelector('.slide-number span.current');

  // if first is active, move .active to second project
  if (projectText[0].classList.contains('active') || (projectImages[0].classList.contains('back') && projectImages[1].classList.contains('next') && projectImages[2].classList.contains('active'))) {
    projectText[0].classList.remove('active');
    projectText[1].classList.add('active');
    projectNum.innerText = '02';

    projectImages[0].classList.remove('back'); projectImages[0].classList.add('next');
    projectImages[1].classList.remove('next'); projectImages[1].classList.add('active');
    projectImages[2].classList.remove('active'); projectImages[2].classList.add('back');
  }
  // if second is active, move .active to third project 

  else if (projectText[1].classList.contains('active') || (projectImages[0].classList.contains('next') && projectImages[1].classList.contains('active') && projectImages[2].classList.contains('back'))) {
    projectText[1].classList.remove('active');
    projectText[2].classList.add('active');
    projectNum.innerText = '03';

    projectImages[0].classList.remove('next'); projectImages[0].classList.add('active');
    projectImages[1].classList.remove('active'); projectImages[1].classList.add('back');
    projectImages[2].classList.remove('back'); projectImages[2].classList.add('next');
  }
  // if third is active, move .active back to first project

  else if (projectText[2].classList.contains('active') || (projectImages[0].classList.contains('active') && projectImages[1].classList.contains('back') && projectImages[2].classList.contains('next'))) {
    projectText[2].classList.remove('active');
    projectText[0].classList.add('active');
    projectNum.innerText = '01';

    projectImages[0].classList.remove('active'); projectImages[0].classList.add('back');
    projectImages[1].classList.remove('back'); projectImages[1].classList.add('next');
    projectImages[2].classList.remove('next'); projectImages[2].classList.add('active');
  }
}

