function moveBackward() {
  const projectImages = document.querySelectorAll('.project-image');
  const projectText = document.querySelectorAll('.project-text .text-container');
  const projectNum = document.querySelector('.slide-number span.current');

  // if first is active, move .active to second project
  if (projectText[0].classList.contains('active')) {
    projectText[0].classList.remove('active');
    projectText[2].classList.add('active');
    projectNum.innerText = '03';

    projectImages[0].classList.remove('back'); projectImages[0].classList.add('active');
    projectImages[1].classList.remove('next'); projectImages[1].classList.add('back');
    projectImages[2].classList.remove('active'); projectImages[2].classList.add('next');
  }
  // if second is active, move .active to third project 

  else if (projectText[1].classList.contains('active')) {
    projectText[1].classList.remove('active');
    projectText[0].classList.add('active');
    projectNum.innerText = '01';

    projectImages[0].classList.remove('next'); projectImages[0].classList.add('back');
    projectImages[1].classList.remove('active'); projectImages[1].classList.add('next');
    projectImages[2].classList.remove('back'); projectImages[2].classList.add('active');
  }
  // if third is active, move .active back to first project

  else if (projectText[2].classList.contains('active')) {
    projectText[2].classList.remove('active');
    projectText[1].classList.add('active');
    projectNum.innerText = '02';

    projectImages[0].classList.remove('active'); projectImages[0].classList.add('next');
    projectImages[1].classList.remove('back'); projectImages[1].classList.add('active');
    projectImages[2].classList.remove('next'); projectImages[2].classList.add('back');
  }
}

function toggleProjectInfo(event){
  let projectCards = document.getElementsByClassName("project-card");
  for (const elem of projectCards) {
    
    if (elem === event.target.parentElement) {
      elem.classList.add('show-info');
    }
    else {
      elem.classList.remove('show-info');
    }
  }
}

function removeProjectInfo(event){
      event.target.classList.remove('show-info');
}

